import './style.css'
import * as d3 from 'd3';
import { legendColor, legendSize } from 'd3-svg-legend';
import $ from 'jquery';
import popper from 'popper.js';
import bootstrap from 'bootstrap';

//let data = d3.tsvParse(fs.readfileSync('data/data.tsv'),'utf-8')
const path = require('./data/data.tsv')

d3.tsv(path).then(function(data) {

  let nodes = d3.nest()
    .key((d)=>d.name)
    .entries(data)

  const width = 700,
        height = 700;

  const margin = {top: 20, right: 20, bottom: 20, left: 20},
    chartWidth = width - margin.left - margin.right,
    chartHeight = height - margin.top - margin.bottom;

  const orbitRadiusDomain = d3.extent(nodes, (d)=>d.values.length)
  const orbitRadius = d3.scaleLinear().domain(orbitRadiusDomain).range([chartWidth/2, 50]);

  const nodeRadiusDomain = d3.extent(nodes, (d)=>{
    return d3.sum(d.values,(m)=>m.relationship_value)
  })

  const nodeRadius = d3.scaleSqrt().domain(nodeRadiusDomain).range([2,8]);

  const continent = d3.map(data, (d)=>d.continent).keys().filter((d)=>d)
  let lists = d3.map(data, (d)=>d.relationship).keys()
  lists = ['All'].concat(lists)

  const colorScale = d3.scaleOrdinal(d3.schemeSet2).domain(continent).unknown('#ccc')

  const chart = d3.select('#viz').append('svg')
     .attr('width', width)
     .attr('height', height)
       .append("g")
       .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

  let mozilla = chart.append('text')
    .attr('x',chartWidth/2)
    .attr('y',chartWidth/2)
    .attr('text-anchor', 'middle')
    .text('Mozilla')

  let orbits =  chart.selectAll(".orbit")
    .data([1,2,3,4,5])
    .enter().append("circle")
    .attr('class','orbit')
    .attr('r', orbitRadius)
    .attr('cx',chartWidth/2)
    .attr('cy',chartWidth/2)
    .attr('stroke','#ddd')
    .attr('stroke-dasharray','3,3')
    .attr('fill','none')

  let node = chart
  .append("g")
  .attr("transform", "translate(" + chartWidth/2 + "," + chartWidth/2 + ")")
  .selectAll(".org")
  .data(nodes)
  .enter().append("circle")
    .attr('class','org')
    .attr("r", (d)=>{
      return nodeRadius(d3.sum(d.values,(m)=>m.relationship_value))
    })
    .attr("fill", (d)=>colorScale(d.values[0].continent))
    .each(function(d){
            $(this).popover({
                'container':'body',
                'title': d.key,
                'placement': 'auto',
                'content': '<ol>' + d.values.map((v)=>'<li>' +v.relationship + '</li>').join('') + '</ol>',
                'trigger':'hover',
                'html':true
            });
      })

  let simulation = d3.forceSimulation(nodes)
      .force("charge", d3.forceCollide().radius((d)=>{
        return nodeRadius(d3.sum(d.values,(m)=>m.relationship_value)) + 2
      }))
      .force("r", d3.forceRadial((d)=>orbitRadius(d.values.length)))
      .on("tick", ticked);

  const legendContinentCont = d3.select('#legend')
    .append('div')
    .append('svg')
    .attr('height', 200)
    .append("g")
    .attr("class", "legendContinent")
    .attr("transform", "translate(0,20)");

  const legendContinent = legendColor()
    .title('Continent:')
    .scale(colorScale)


  legendContinentCont.call(legendContinent);

  const legendCircleCont = d3.select('#legend')
    .append('div')
    .append('svg')
    .append("g")
      .attr("class", "legendCircle")
      .attr("transform", "translate(0,20)");

  const legendCircle = legendSize()
    .shape('circle')
    .labelFormat(d3.format(".0f"))
    .scale(nodeRadius)
    .orient('horizontal')
    .shapePadding(20)
    .labelOffset(20)
    .labels(["further",
  "", "", "", "closer"])
    .title('Relation:')

  legendCircleCont.call(legendCircle);

  const legendOrbitCont = d3.select('#legend')
    .append('div')
    .append('svg')
    .attr('width',350)
    .attr('height',150)

  const orbitLegendRadius = d3.scaleLinear().domain(orbitRadiusDomain).range([200, 50]);
  let orbitslegend =  legendOrbitCont
  .selectAll(".orbitLegend")
    .data([1,2,3,4,5])
    .enter().append("circle")
    .attr('class','orbitLegend')
    .attr('r', orbitLegendRadius)
    .attr('cx',0)
    .attr('cy',150)
    .attr('stroke','#ddd')
    .attr('stroke-dasharray','3,3')
    .attr('fill','none')

  let orbitslegendText =  legendOrbitCont
    .selectAll(".orbitLegendText")
      .data([1,2,3,4,5])
      .enter().append("text")
      .attr('class','orbitLegendText')
      .attr('x', orbitLegendRadius)
      .attr('y',150)
      .attr('text-anchor','middle')
      .text((d=>d))

  legendOrbitCont
    .append('text')
    .attr('x',0)
    .attr('y',15)
    .text('Presence:')

  let listsModel = d3.map(data, (d)=>d.relationship).keys()
  let buttons = d3.select('#buttons')
  .selectAll(".filter")
    .data(listsModel.sort())
    .enter()
    .append("div")
    .attr('class', 'filter custom-control custom-checkbox custom-control-inline')

  buttons.append('input')
    .attr('type','checkbox')
    .attr('class','custom-control-input')
    .attr('id',(d,i)=>'custom_'+i)
    .attr('checked', 'checked')
    .on("change",(d)=>{

      let selected = []
      d3.select('#buttons')
        .selectAll('input:checked')
        .each((f)=>{
          selected.push(f)
        })

      node
        .attr('opacity',0.1)
        .filter((f)=>{
          return f.values.map((v)=>v.relationship).some(v => selected.includes(v))
          //return f.values.map((v)=>v.relationship).indexOf(d)>-1?true:false
        })
        .attr('opacity',1)

    })

  buttons.append('label')
    .attr('type','checkbox')
    .attr('class','custom-control-label')
    .attr('for',(d,i)=>'custom_'+i)
    .text((d)=>d)


  function ticked() {
    node
        .attr("cx", (d)=>d.x)
        .attr("cy", (d)=>d.y);
  }

  function findOne(haystack, arr) {
    return arr.some(function (v) {
        return haystack.indexOf(v) >= 0;
    });
};


});
